'use client';

import { useState } from 'react';
import Link from 'next/link';
import { useTheme } from 'next-themes';
import {
    Shield,
    Bell,
    User,
    Settings,
    Moon,
    Sun,
    Globe,
    Lock,
    Database,
    RefreshCw,
    Save,
    AlertTriangle,
    CheckCircle2
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { Slider } from '@/components/ui/slider';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { SettingsModal } from '@/components/settings-modal';

export default function SettingsPage() {
    const { theme, setTheme, resolvedTheme } = useTheme();
    const [isDirty, setIsDirty] = useState(false);

    // Settings state
    const [settings, setSettings] = useState({
        darkMode: resolvedTheme === 'dark',
        notifications: true,
        autoScan: false,
        phishingThreshold: 85,
        suspiciousThreshold: 55,
        maxOverridesPerHour: 5,
        cacheEnabled: true,
        cacheTtl: 3600,
    });

    const handleChange = (key: string, value: boolean | number) => {
        setSettings(prev => ({ ...prev, [key]: value }));
        setIsDirty(true);

        if (key === 'darkMode') {
            setTheme(value ? 'dark' : 'light');
        }
    };

    const handleSave = () => {
        // TODO: Save settings to backend
        setIsDirty(false);
        alert('Settings saved successfully!');
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-slate-50 to-slate-100 dark:from-slate-950 dark:to-slate-900">
            {/* Navigation */}
            <nav className="sticky top-0 z-50 bg-white/80 dark:bg-slate-900/80 backdrop-blur-md border-b border-slate-200 dark:border-slate-800">
                <div className="container mx-auto px-4">
                    <div className="flex h-16 items-center justify-between">
                        <div className="flex items-center gap-8">
                            <Link href="/" className="flex items-center gap-2">
                                <div className="h-9 w-9 rounded-lg bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                    <Shield className="h-5 w-5 text-white" />
                                </div>
                                <span className="font-bold text-lg">PhishGuard</span>
                            </Link>
                            <div className="hidden md:flex items-center gap-6">
                                <Link href="/dashboard" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Dashboard</Link>
                                <Link href="/history" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">History</Link>
                                <Link href="/threat-map" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Threat Map</Link>
                                <Link href="/settings" className="text-sm font-medium text-slate-900 dark:text-white">Settings</Link>
                            </div>
                        </div>
                        <div className="flex items-center gap-3">
                            <Button variant="ghost" size="icon">
                                <Bell className="h-5 w-5" />
                            </Button>
                            <SettingsModal />
                            <div className="h-9 w-9 rounded-full bg-gradient-to-br from-amber-400 to-orange-500 flex items-center justify-center">
                                <User className="h-5 w-5 text-white" />
                            </div>
                        </div>
                    </div>
                </div>
            </nav>

            {/* Header */}
            <div className="border-b border-slate-200 dark:border-slate-800 bg-white/50 dark:bg-slate-900/50">
                <div className="container mx-auto px-4 py-8">
                    <div className="flex items-center justify-between">
                        <div>
                            <div className="flex items-center gap-3 mb-2">
                                <Settings className="h-8 w-8 text-indigo-600" />
                                <h1 className="text-3xl font-bold text-slate-900 dark:text-white">Settings</h1>
                            </div>
                            <p className="text-slate-600 dark:text-slate-400">Configure your PhishGuard preferences</p>
                        </div>
                        {isDirty && (
                            <Button onClick={handleSave} className="gap-2">
                                <Save className="h-4 w-4" />
                                Save Changes
                            </Button>
                        )}
                    </div>
                </div>
            </div>

            <div className="container mx-auto px-4 py-8 max-w-4xl">
                <div className="space-y-8">
                    {/* Appearance */}
                    <section className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        <div className="px-6 py-4 border-b border-slate-200 dark:border-slate-700">
                            <div className="flex items-center gap-2">
                                {resolvedTheme === 'dark' ? <Moon className="h-5 w-5 text-indigo-600" /> : <Sun className="h-5 w-5 text-indigo-600" />}
                                <h2 className="font-semibold text-slate-900 dark:text-white">Appearance</h2>
                            </div>
                        </div>
                        <div className="p-6 space-y-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">Dark Mode</Label>
                                    <p className="text-sm text-slate-500 mt-1">Switch between light and dark themes</p>
                                </div>
                                <Switch
                                    checked={resolvedTheme === 'dark'}
                                    onCheckedChange={(checked) => handleChange('darkMode', checked)}
                                />
                            </div>
                            <Separator />
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">Notifications</Label>
                                    <p className="text-sm text-slate-500 mt-1">Receive alerts for new threats</p>
                                </div>
                                <Switch
                                    checked={settings.notifications}
                                    onCheckedChange={(checked) => handleChange('notifications', checked)}
                                />
                            </div>
                        </div>
                    </section>

                    {/* API Configuration */}
                    <section className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        <div className="px-6 py-4 border-b border-slate-200 dark:border-slate-700">
                            <div className="flex items-center gap-2">
                                <Globe className="h-5 w-5 text-indigo-600" />
                                <h2 className="font-semibold text-slate-900 dark:text-white">API Configuration</h2>
                            </div>
                        </div>
                        <div className="p-6 space-y-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">API Endpoint</Label>
                                    <p className="text-sm text-slate-500 mt-1">Backend server URL</p>
                                </div>
                                <div className="flex items-center gap-2">
                                    <code className="text-sm bg-slate-100 dark:bg-slate-900 px-3 py-1 rounded">
                                        http://127.0.0.1:5000
                                    </code>
                                    <Badge variant="outline" className="border-green-500 text-green-500">
                                        <CheckCircle2 className="h-3 w-3 mr-1" />
                                        Connected
                                    </Badge>
                                </div>
                            </div>
                            <Separator />
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">Auto-scan URLs</Label>
                                    <p className="text-sm text-slate-500 mt-1">Automatically scan URLs when pasted</p>
                                </div>
                                <Switch
                                    checked={settings.autoScan}
                                    onCheckedChange={(checked) => handleChange('autoScan', checked)}
                                />
                            </div>
                        </div>
                    </section>

                    {/* Safety Thresholds */}
                    <section className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        <div className="px-6 py-4 border-b border-slate-200 dark:border-slate-700">
                            <div className="flex items-center justify-between">
                                <div className="flex items-center gap-2">
                                    <Lock className="h-5 w-5 text-indigo-600" />
                                    <h2 className="font-semibold text-slate-900 dark:text-white">Safety Thresholds</h2>
                                </div>
                                <Badge variant="secondary">Admin Only</Badge>
                            </div>
                        </div>
                        <div className="p-6 space-y-6">
                            <div className="bg-amber-50 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800 rounded-lg p-4">
                                <div className="flex items-start gap-2">
                                    <AlertTriangle className="h-5 w-5 text-amber-600 mt-0.5" />
                                    <div>
                                        <p className="text-sm font-medium text-amber-800 dark:text-amber-200">
                                            Caution: Modifying thresholds affects detection accuracy
                                        </p>
                                        <p className="text-sm text-amber-700 dark:text-amber-300 mt-1">
                                            Lower thresholds increase false positives, higher thresholds may miss threats.
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className="space-y-4">
                                <div>
                                    <div className="flex items-center justify-between mb-2">
                                        <Label>Phishing Threshold</Label>
                                        <span className="text-sm font-medium text-slate-900 dark:text-white">
                                            {settings.phishingThreshold}%
                                        </span>
                                    </div>
                                    <Slider
                                        value={[settings.phishingThreshold]}
                                        onValueChange={([value]) => handleChange('phishingThreshold', value)}
                                        min={50}
                                        max={100}
                                        step={1}
                                    />
                                    <p className="text-xs text-slate-500 mt-1">
                                        URLs with risk score above this are marked as PHISHING
                                    </p>
                                </div>

                                <div>
                                    <div className="flex items-center justify-between mb-2">
                                        <Label>Suspicious Threshold</Label>
                                        <span className="text-sm font-medium text-slate-900 dark:text-white">
                                            {settings.suspiciousThreshold}%
                                        </span>
                                    </div>
                                    <Slider
                                        value={[settings.suspiciousThreshold]}
                                        onValueChange={([value]) => handleChange('suspiciousThreshold', value)}
                                        min={20}
                                        max={settings.phishingThreshold - 1}
                                        step={1}
                                    />
                                    <p className="text-xs text-slate-500 mt-1">
                                        URLs with risk score above this are marked as SUSPICIOUS
                                    </p>
                                </div>

                                <div>
                                    <div className="flex items-center justify-between mb-2">
                                        <Label>Max Overrides Per Hour</Label>
                                        <span className="text-sm font-medium text-slate-900 dark:text-white">
                                            {settings.maxOverridesPerHour}
                                        </span>
                                    </div>
                                    <Slider
                                        value={[settings.maxOverridesPerHour]}
                                        onValueChange={([value]) => handleChange('maxOverridesPerHour', value)}
                                        min={1}
                                        max={20}
                                        step={1}
                                    />
                                    <p className="text-xs text-slate-500 mt-1">
                                        Safety budget for governance overrides (triggers freeze when exhausted)
                                    </p>
                                </div>
                            </div>
                        </div>
                    </section>

                    {/* Cache Settings */}
                    <section className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        <div className="px-6 py-4 border-b border-slate-200 dark:border-slate-700">
                            <div className="flex items-center gap-2">
                                <Database className="h-5 w-5 text-indigo-600" />
                                <h2 className="font-semibold text-slate-900 dark:text-white">Cache Settings</h2>
                            </div>
                        </div>
                        <div className="p-6 space-y-6">
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">Enable Caching</Label>
                                    <p className="text-sm text-slate-500 mt-1">Cache analysis results for faster responses</p>
                                </div>
                                <Switch
                                    checked={settings.cacheEnabled}
                                    onCheckedChange={(checked) => handleChange('cacheEnabled', checked)}
                                />
                            </div>
                            <div className="flex items-center justify-between">
                                <div>
                                    <Label className="text-base">Clear Cache</Label>
                                    <p className="text-sm text-slate-500 mt-1">Remove all cached analysis results</p>
                                </div>
                                <Button variant="outline" className="gap-2">
                                    <RefreshCw className="h-4 w-4" />
                                    Clear Now
                                </Button>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    );
}
